export default function alertPopUp(alertTxt) {
    let arertClose = document.querySelector('#alert-close')

    setTimeout(() => { document.querySelector('#alert').classList.add('uk-transition-slide-right') }, 6000);
    arertClose.onclick = () => {
        document.querySelector('#alert').classList.add('uk-transition-slide-right');
    }
    document.querySelector('#alert-text').textContent = alertTxt;
}